.card__container {
  margin: 20px;
  border-radius: 15px !important;
  -webkit-box-shadow: 0px 1px 27px -3px rgba(255,255,255,1);
  -moz-box-shadow: 0px 1px 27px -3px rgba(255,255,255,1);
  box-shadow: 0px 1px 27px -3px rgba(255,255,255,1) !important;
}

.card__media {
  height: 300px;
  width: 400px;
  transition: all 0.2s ease-in-out;

  @media screen and (max-width: 1350px) {
    height: 250px;
    width: 350px;
  }

  @media screen and (max-width: 1075px) {
    height: 200px;
    width: 300px;
  }

}

.card__backContent {
  height: 340px;
  width: 400px;
  transition: all 0.2s ease-in-out;

  @media screen and (max-width: 1350px) {
    height: 290px;
    width: 350px;
  }

  @media screen and (max-width: 1075px) {
    height: 240px;
    width: 300px;
  }

}

.card__list {
  padding: 10px !important;
}

.card__frontContent {
  width: 400px;
  font-weight: 520 !important;
  color: #010606 !important;

  @media screen and (max-width: 1350px) {
    width: 350px;
  }

  @media screen and (max-width: 1075px) {
    width: 300px;
  }

}

.scrollable{
  @media screen and (max-width: 1350px) {
    overflow-y: scroll !important;
  }
}

.card__media > img {
  width: 400px;
  height: 300px;
}

.card__media > p {
  padding: 10px;
}

